.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
}

body {
  color: #000;
  background-color: #fff5f6;
  font-family: DM Sans, sans-serif;
}

.title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #d41443;
  margin-top: 16px;
  font-family: Titan One, cursive;
  font-size: 34px;
  line-height: 1.15;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.no-scroll {
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.button {
  cursor: pointer;
  border: none;
  border-radius: 20px;
  margin: 0 auto;
  padding: 10px 32px;
  font-family: inherit;
  font-size: 16px;
  line-height: 1.31;
  display: block;
}

.button--red {
  color: #fff;
  background-color: #d41443;
}

.button--white {
  color: #d41443;
  background-color: #fff;
  transition: background-color .3s linear;
}

.button--white:hover, .button--white:focus {
  background-color: #ecebeb;
}

